<template>
  <div class="aside-menu" v-if="!itemObj.isHide">
    <el-menu-item :index="itemObj.path"  v-if="!itemObj.children">
        <i v-if="itemObj.iCons" :class="itemObj.iCons" style="margin-right:8px;"></i>
        <span slot="title">{{itemObj.name}}</span>
    </el-menu-item>
    <el-submenu :index="itemObj.path" v-else>
        <template slot="title">
            <i v-if="itemObj.iCons" :class="itemObj.iCons" style="margin-right:8px;"></i>
            <span>{{itemObj.name}}</span>
        </template>
        <NavItem v-for="child in itemObj.children" :key="child.path" :item="child"/>
    </el-submenu>
  </div>
</template>

<script>
export default {
    name:'NavItem',
    data(){
        return{

        }
    },
    props:['item'],
    computed:{
        itemObj(){
            if(this.item.name == '库存分组'){//非M8账号不显示库存分组
                this.item.isHide = this.$store.state.base.erp.hasM8 ? false : true;
            }
            return this.item;
        }
    }
}
</script>

<style lang="less" scoped>
.aside-menu{
    
    /deep/.el-submenu__icon-arrow{
        font-size:12px;
    }
    /deep/.el-submenu .el-menu-item{
        min-width: 140px;
        max-width: 140px;
        padding: 0 24px!important;
    }
}

</style>